* {
  box-sizing: border-box;
}

#myUL {
  height: 200px;
  margin-top: 0;
  margin-left: 50px;
  margin-right: 50px;
  padding: 0;
  overflow-y: scroll;
}

#myUL li {
  cursor: pointer;
  color: #000;
  -webkit-user-select: none;
  user-select: none;
  background: #fff;
  border-radius: 15px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 12px 8px 12px 40px;
  font-size: 18px;
  transition: all .2s;
  position: relative;
}

#myUL li:hover {
  background: #9eb2f8;
}

#myUL li.checked {
  color: #fff;
  background: #3560fa;
  text-decoration: line-through;
}

#myUL li.checked:before {
  content: "";
  height: 15px;
  width: 7px;
  border: 2px solid #fff;
  border-width: 0 2px 2px 0;
  position: absolute;
  top: 10px;
  left: 16px;
  transform: rotate(45deg);
}

.close {
  padding: 12px 16px;
  position: absolute;
  top: 0;
  right: 0;
}

.close:hover {
  color: #fff;
  background-color: #f44336;
}

.header {
  color: #fff;
  background-color: #9eb2f8;
  border-radius: 15px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 50px 10% 0;
  padding: 30px 40px;
  display: flex;
}

.goalsContainer {
  background-color: #eaeaea;
  border-radius: 15px;
  margin-left: 10%;
  margin-right: 10%;
}

.header:after {
  content: "";
  clear: both;
  display: table;
}

input {
  color: #1c1c1c;
  width: 75%;
  border: none;
  border-radius: 10px;
  justify-content: center;
  margin: 0;
  padding: 10px;
  font-size: 16px;
}

.addBtn {
  width: 170px;
  color: #1c1c1c;
  text-align: center;
  cursor: pointer;
  background: #fff;
  border-radius: 10px;
  justify-content: center;
  margin-left: 10px;
  padding: 10px;
  font-size: 20px;
  transition: all .2s;
}

.addBtn:hover {
  color: #fff;
  background-color: #3560fa;
}

